"use client"
import { Canvas, useThree } from "@react-three/fiber"
import { OrbitControls, Grid, PerspectiveCamera, Text } from "@react-three/drei"
import { Suspense, useRef, useState, useEffect } from "react"
import * as THREE from "three"

interface Container {
  id: string
  name: string
  type: string
  dimensions: string
  weight: string
  icon: string
}

interface ViewportCanvasProps {
  container: Container | null
  items: any[]
}

// Parse dimensions like "1203.2 x 235.2 x 238.5 cm" into meters
function parseDimensions(dimensions: string) {
  const cleaned = dimensions.replace(' cm', '').trim()
  const parts = cleaned.split('x').map(s => parseFloat(s.trim()))
  
  if (parts.length === 3) {
    return {
      length: parts[0] / 100, // Convert cm to meters
      width: parts[1] / 100,
      height: parts[2] / 100
    }
  }
  
  return { length: 12, width: 2.4, height: 2.4 }
}

export default function ViewportCanvas({ container, items }: ViewportCanvasProps) {
  const [targetPosition, setTargetPosition] = useState<[number, number, number] | null>(null)

  return (
    <div className="h-full w-full relative">
      <Canvas shadows>
        <CameraController targetPosition={targetPosition} />
        <PerspectiveCamera makeDefault position={[15, 10, 15]} fov={50} />
        <OrbitControls makeDefault enableDamping dampingFactor={0.05} />
        
        {/* Lighting */}
        <ambientLight intensity={0.5} />
        <directionalLight position={[10, 10, 5]} intensity={1} castShadow shadow-mapSize={[2048, 2048]} />
        <directionalLight position={[-10, 10, -5]} intensity={0.3} />
        
        {/* Grid Floor */}
        <Grid
          args={[50, 50]}
          cellSize={1}
          cellThickness={0.5}
          cellColor="#cccccc"
          sectionSize={5}
          sectionThickness={1}
          sectionColor="#999999"
          fadeDistance={50}
          fadeStrength={1}
          position={[0, 0, 0]}
        />
        
        {/* 3D Container and Items */}
        <Suspense fallback={null}>
          {container ? (
            <>
              <Container3D container={container} items={items} />
              {/* Render staged items (items without x,y,z position) in a grid */}
              <StagingArea items={items.filter(item => item.x === undefined)} />
            </>
          ) : (
            <EmptyState />
          )}
        </Suspense>
        
        {/* Measurement Axis */}
        <group position={[-8, 0, -8]}>
          {/* X Axis (Red) */}
          <mesh position={[1.5, 0.05, 0]} rotation={[0, 0, Math.PI / 2]}>
            <cylinderGeometry args={[0.02, 0.02, 3]} />
            <meshBasicMaterial color="#ff0000" />
          </mesh>
          <mesh position={[3, 0.05, 0]} rotation={[0, 0, -Math.PI / 2]}>
            <coneGeometry args={[0.08, 0.2]} />
            <meshBasicMaterial color="#ff0000" />
          </mesh>
          
          {/* Y Axis (Green) */}
          <mesh position={[0, 1.55, 0]}>
            <cylinderGeometry args={[0.02, 0.02, 3]} />
            <meshBasicMaterial color="#00ff00" />
          </mesh>
          <mesh position={[0, 3.1, 0]}>
            <coneGeometry args={[0.08, 0.2]} />
            <meshBasicMaterial color="#00ff00" />
          </mesh>
          
          {/* Z Axis (Blue) */}
          <mesh position={[0, 0.05, 1.5]} rotation={[Math.PI / 2, 0, 0]}>
            <cylinderGeometry args={[0.02, 0.02, 3]} />
            <meshBasicMaterial color="#0000ff" />
          </mesh>
          <mesh position={[0, 0.05, 3]} rotation={[Math.PI / 2, 0, 0]}>
            <coneGeometry args={[0.08, 0.2]} />
            <meshBasicMaterial color="#0000ff" />
          </mesh>
          
          {/* Axis Labels */}
          <Suspense fallback={null}>
            <Text position={[3.5, 0.05, 0]} fontSize={0.3} color="#ff0000">
              X
            </Text>
            <Text position={[0, 3.5, 0]} fontSize={0.3} color="#00ff00">
              Y
            </Text>
            <Text position={[0, 0.05, 3.5]} fontSize={0.3} color="#0000ff">
              Z
            </Text>
          </Suspense>
        </group>
      </Canvas>
      
      {/* ViewCube - Camera Control */}
      <ViewCube onViewChange={setTargetPosition} />
    </div>
  )
}

// Camera Controller Component
function CameraController({ targetPosition }: { targetPosition: [number, number, number] | null }) {
  const { camera } = useThree()
  
  useEffect(() => {
    if (targetPosition) {
      camera.position.set(...targetPosition)
      camera.lookAt(0, 0, 0)
      camera.updateProjectionMatrix()
    }
  }, [targetPosition, camera])
  
  return null
}

// ViewCube Component for camera control
function ViewCube({ onViewChange }: { onViewChange: (position: [number, number, number]) => void }) {
  const views: { name: string; position: [number, number, number] }[] = [
    { name: 'Front', position: [0, 5, 15] },
    { name: 'Back', position: [0, 5, -15] },
    { name: 'Left', position: [-15, 5, 0] },
    { name: 'Right', position: [15, 5, 0] },
    { name: 'Top', position: [0, 20, 0.1] },
    { name: 'Bottom', position: [0, -20, 0.1] },
  ]

  return (
    <div className="absolute top-4 right-4 bg-white rounded-lg shadow-lg p-2 border border-gray-200">
      <div className="grid grid-cols-3 gap-1">
        {/* Top row */}
        <div></div>
        <button
          onClick={() => onViewChange(views[4].position)}
          className="w-12 h-12 bg-blue-50 hover:bg-blue-100 border border-blue-200 rounded text-xs font-semibold text-blue-700 transition-colors"
          title="Top View"
        >
          Top
        </button>
        <div></div>
        
        {/* Middle row */}
        <button
          onClick={() => onViewChange(views[2].position)}
          className="w-12 h-12 bg-blue-50 hover:bg-blue-100 border border-blue-200 rounded text-xs font-semibold text-blue-700 transition-colors"
          title="Left View"
        >
          Left
        </button>
        <button
          onClick={() => onViewChange(views[0].position)}
          className="w-12 h-12 bg-blue-100 hover:bg-blue-200 border-2 border-blue-400 rounded text-xs font-semibold text-blue-800 transition-colors"
          title="Front View"
        >
          Front
        </button>
        <button
          onClick={() => onViewChange(views[3].position)}
          className="w-12 h-12 bg-blue-50 hover:bg-blue-100 border border-blue-200 rounded text-xs font-semibold text-blue-700 transition-colors"
          title="Right View"
        >
          Right
        </button>
        
        {/* Bottom row */}
        <div></div>
        <button
          onClick={() => onViewChange(views[5].position)}
          className="w-12 h-12 bg-blue-50 hover:bg-blue-100 border border-blue-200 rounded text-xs font-semibold text-blue-700 transition-colors"
          title="Bottom View"
        >
          Bot
        </button>
        <button
          onClick={() => onViewChange(views[1].position)}
          className="w-12 h-12 bg-blue-50 hover:bg-blue-100 border border-blue-200 rounded text-xs font-semibold text-blue-700 transition-colors"
          title="Back View"
        >
          Back
        </button>
      </div>
    </div>
  )
}

// Empty state when no container selected
function EmptyState() {
  return (
    <Text
      position={[0, 2, 0]}
      fontSize={0.8}
      color="#999999"
      anchorY="middle"
      anchorX="center"
    >
      Select a container to begin
    </Text>
  )
}

// Container 3D Component
function Container3D({ container, items }: { container: Container; items: any[] }) {
  const { length, width, height } = parseDimensions(container.dimensions)
  
  return (
    <group position={[0, height / 2, 0]}>
      {/* Semi-transparent container box */}
      <mesh>
        <boxGeometry args={[length, height, width]} />
        <meshStandardMaterial 
          color="#4a90e2" 
          transparent 
          opacity={0.15}
        />
      </mesh>
      
      {/* Container edges for better visibility */}
      <lineSegments>
        <edgesGeometry args={[new THREE.BoxGeometry(length, height, width)]} />
        <lineBasicMaterial color="#2c5aa0" linewidth={2} />
      </lineSegments>
      
      {/* Floor inside container */}
      <mesh 
        position={[0, -height / 2 + 0.01, 0]} 
        rotation={[-Math.PI / 2, 0, 0]} 
        receiveShadow
      >
        <planeGeometry args={[length, width]} />
        <meshStandardMaterial color="#e8e8e8" side={2} />
      </mesh>
      
      {/* Container name label */}
      <Text
        position={[0, height / 2 + 0.5, 0]}
        fontSize={0.4}
        color="#2c5aa0"
        anchorY="bottom"
      >
        {container.name}
      </Text>
      
      {/* Container info label */}
      <Text
        position={[0, height / 2 + 0.2, 0]}
        fontSize={0.25}
        color="#666666"
        anchorY="bottom"
      >
        {container.dimensions} • {container.weight}
      </Text>
      
      {/* Dimension labels */}
      <Text
        position={[0, -height / 2 - 0.3, width / 2 + 0.3]}
        fontSize={0.3}
        color="#ff0000"
      >
        L: {(length * 100).toFixed(1)} cm
      </Text>
      
      <Text
        position={[length / 2 + 0.3, -height / 2 - 0.3, 0]}
        fontSize={0.3}
        color="#0000ff"
        rotation={[0, Math.PI / 2, 0]}
      >
        W: {(width * 100).toFixed(1)} cm
      </Text>
      
      <Text
        position={[length / 2 + 0.3, 0, width / 2 + 0.3]}
        fontSize={0.3}
        color="#00ff00"
      >
        H: {(height * 100).toFixed(1)} cm
      </Text>
      
      {/* Items inside container (only items with x,y,z position) */}
      {items.filter(item => item.x !== undefined).map((item, index) => (
        <Item3D key={item.id || index} item={item} containerHeight={height} />
      ))}
    </group>
  )
}

// Item 3D Component
function Item3D({ item, containerHeight }: { item: any; containerHeight: number }) {
  // Convert cm to meters
  const itemLength = (item.length || 100) / 100
  const itemWidth = (item.width || 100) / 100
  const itemHeight = (item.height || 100) / 100
  
  const color = item.color || '#4a90e2'
  
  // Item is positioned inside container
  const x = item.x || 0
  const y = (item.y || itemHeight / 2) - containerHeight / 2
  const z = item.z || 0
  
  return (
    <mesh position={[x, y, z]} castShadow receiveShadow>
      <boxGeometry args={[itemLength, itemHeight, itemWidth]} />
      <meshStandardMaterial color={color} />
      
      <lineSegments>
        <edgesGeometry args={[new THREE.BoxGeometry(itemLength, itemHeight, itemWidth)]} />
        <lineBasicMaterial color="#000000" opacity={0.3} transparent />
      </lineSegments>
    </mesh>
  )
}

// Staging Area Component - arranges items in a grid in front of container
function StagingArea({ items }: { items: any[] }) {
  const itemsPerRow = 3
  const spacing = 0.3
  let boxIndex = 0
  
  return (
    <>
      {items.map((item, itemIndex) => {
        const itemLength = (item.length || 100) / 100
        const itemWidth = (item.width || 100) / 100
        const itemHeight = (item.height || 100) / 100
        const color = item.color || '#4a90e2'
        const count = item.count || 1
        
        const boxes = []
        for (let i = 0; i < count; i++) {
          const row = Math.floor(boxIndex / itemsPerRow)
          const col = boxIndex % itemsPerRow
          
          // Position in front of container (positive Z)
          const z = 8 + row * 2  // In front, moving further away with each row
          const x = col * (1.5 + spacing) - ((itemsPerRow - 1) * (1.5 + spacing)) / 2  // Spread across X
          const y = itemHeight / 2
          
          boxes.push(
            <group key={`${item.id}-${i}`}>
              <mesh 
                position={[x, y, z]}
                castShadow 
                receiveShadow
              >
                <boxGeometry args={[itemLength, itemHeight, itemWidth]} />
                <meshStandardMaterial color={color} />
                
                <lineSegments>
                  <edgesGeometry args={[new THREE.BoxGeometry(itemLength, itemHeight, itemWidth)]} />
                  <lineBasicMaterial color="#000000" opacity={0.3} transparent />
                </lineSegments>
              </mesh>
              
              {/* Item label - only show for first box of each item */}
              {i === 0 && (
                <Text
                  position={[x, itemHeight + 0.3, z]}
                  fontSize={0.2}
                  color="#333333"
                  anchorY="bottom"
                >
                  {item.name} {count > 1 ? `(x${count})` : ''}
                </Text>
              )}
            </group>
          )
          boxIndex++
        }
        
        return boxes
      })}
    </>
  )
}